@tailwind base;
@tailwind components;
@tailwind utilities;

/* Typography and layout base */
body {
  @apply font-sans bg-gradient-to-tr from-white via-blue-50 to-white text-gray-800 leading-relaxed;
}

/* Enhance scroll behavior */
html {
  scroll-behavior: smooth;
}

/* Headings */
h1, h2, h3, h4, h5, h6 {
  @apply tracking-tight font-extrabold text-blue-800;
}

/* Buttons */
.button-primary {
  @apply bg-gradient-to-r from-blue-600 to-indigo-600 text-white font-semibold py-2 px-6 rounded-lg shadow-md transition transform hover:scale-105 hover:from-indigo-600 hover:to-blue-700;
}

/* Cards */
.card {
  @apply rounded-xl shadow-lg p-6 bg-white hover:shadow-2xl transition duration-300 ease-in-out;
}

/* Image hover effect */
.card-image-hover {
  @apply rounded-lg shadow-lg transition-transform duration-300 transform hover:scale-105 hover:brightness-110;
}

/* Input and textarea */
input, textarea {
  @apply border border-gray-300 rounded-md px-4 py-3 w-full focus:outline-none focus:ring-2 focus:ring-blue-600 transition duration-200;
}

/* Section titles */
.section-title {
  @apply text-4xl md:text-5xl font-bold text-center text-blue-800 mb-10;
}

/* Smooth fade-in animation */
.fade-in {
  animation: fadeIn 1s ease-in-out;
}
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Link styles */
a {
  @apply text-blue-600 hover:text-indigo-600 transition no-underline;
}


/* Mobile nav enhancements */
.mobile-nav-link {
  @apply block px-4 py-2 text-sm font-medium hover:text-blue-700;
}

/* Footer styling (if you extend it) */
footer {
  @apply bg-blue-100 text-gray-700 py-6 text-center mt-20;
}

/* Divider styles */
.section-divider {
  @apply my-20 border-t-2 border-dotted border-blue-300 w-1/2 mx-auto;
}

/* Gradient text utility */
.gradient-text {
  background: linear-gradient(to right, #2563eb, #4f46e5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
